import React from 'react';
import { default as MuiInputAdornment, InputAdornmentProps } from '@mui/material/InputAdornment';

const InputAdornment = React.forwardRef<HTMLElement, InputAdornmentProps>( ( props, ref ) => {
	return (
		<MuiInputAdornment
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<InputAdornmentProps>;

export default InputAdornment;
