import React from 'react';
import { default as MuiTextField, TextFieldProps } from '@mui/material/TextField';

const TextField = React.forwardRef<HTMLElement, TextFieldProps>( ( props, ref ) => {
	return (
		<MuiTextField
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TextFieldProps>;

export default TextField;
