import React from 'react';
import { default as MuiSvgIcon, SvgIconProps } from '@mui/material/SvgIcon';

const SvgIcon = React.forwardRef<HTMLElement, SvgIconProps>( ( props, ref ) => {
	return (
		<MuiSvgIcon
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SvgIconProps>;

export default SvgIcon;
